import React from 'react';
import axios from 'axios';
import { Row,Col } from 'antd';

import TagComponent from '../component/tags/tags';
import MyBlogComponent from '../component/myBlogs/myBlogs';

class MyBlogContainer extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            blogs: [],
            tags: []
        }
        this.getBlogs = this.getBlogs.bind(this);
        this.getTags = this.getTags.bind(this);
        this.handleCheckTag = this.handleCheckTag.bind(this);
    }

    componentDidMount(){
        this.getBlogs();
        this.getTags();
    }

    getBlogs(){
        var that = this;
        axios.get('/configure/articles/blogs.json')
            .then(function(result){
                that.setState({
                    blogs: result.data
                });
            })
    }

    getTags(){
        var that = this;
        axios.get('/configure/articles/tags.json')
            .then(function(result){
                that.setState({
                    tags: result.data.tags
                });
            });
    }

    handleCheckTag(tagIndex, checked){
        var newTagState = Object.assign([], this.state.tags);
        newTagState[tagIndex].checked = checked;
        this.setState({tags:newTagState});
    }

    render(){
        return (
        <Row
            style = {{'height':'100%'}}>
            <Col 
                span = {4}
                style = {{
                    'height': '100%',
                    'borderRight': '0.1rem grey solid'
                }}>
                    <TagComponent
                        tags = {this.state.tags}
                        handleCheckTag = {this.handleCheckTag}></TagComponent>
            </Col>
            <Col
                span = {16}
                style = {{
                    'height': '100%',
                    'padding': '1rem',
                    'paddingLeft': '3rem'
                }}>
                    <MyBlogComponent></MyBlogComponent>
            </Col>
        </Row>);
    }
}

export default MyBlogContainer;